.gallery{
  padding-top: 50px;
  padding-bottom: 50px;
  background-color: black;
  @include big-desktop{

  }
}
.gallery__title{
  color: white;
  text-align: center;
  font-size: 60px;
}
.gallery__item img{
 border-radius: 15px;
}
.gallery__item source{
  border-radius: 15px;
 }

.swiper-pagination-fraction{
  width: 200px;
}

.gallery-swiper{
  margin-bottom: 45px;
  height: 381px;
}
.gallery__slider-btn{
  @include bg-cover;

  width: 50px;
  height: 48px;
}

.slider__btn{
  display: flex;
  justify-content: center;
}
.swiper-pagination{
  position: relative;
}
.swiper-pagination-bullet{
  background-color: white;
}
.swiper-pagination-bullet-active{
  background-color:#94794e;
}
.slider__btn{
  align-items: center;
}
.swiper-pagination-bullet{
  width: var(--swiper-pagination-bullet-width, var(--swiper-pagination-bullet-size, 18px));
  height: var(--swiper-pagination-bullet-height, var(--swiper-pagination-bullet-size, 18px));
  opacity: var(--swiper-pagination-bullet-inactive-opacity, 1);
}

.gallery__item a img{
  height: 380px;
}
