.order{
  padding-top: 70px;
  padding-bottom: 70px;
}
.order_form-title{
  font-weight: 400;
font-size: 25px;
line-height: 28px;
}
.order_form{
  display: flex;
  justify-content: space-between;
  @include tablet{
    flex-direction: column;
  }
}
.order_form-title{
  text-align: center;
  margin-bottom: 10px;
}
.order_form-input{
  background: #FFFFFF;
border: 1px solid #000000;
box-shadow: 0px 8px 8px rgba(0, 0, 0, 0.5);
border-radius: 10px;
width: 305px;
height: 50px;
font-weight: 400;
font-size: 20px;
line-height: 18px;
text-align: center;
outline: none;
@include mobile{
  width: 290px;
height: 50px;
}
}

.order_form-input::placeholder{
  font-size: 15px;
  @include small-tablet{
    font-size: 14px;
  }
}
::-webkit-input-placeholder {
  text-align:center;
  }

  :-moz-placeholder { /* Firefox 18- */
     text-align:center;
  }

  ::-moz-placeholder {  /* Firefox 19+ */
     text-align:center;
  }

  :-ms-input-placeholder {
     text-align:center;
  }

  .order_form-button{
    cursor: pointer;
  background-color: black;
  color: white;
  font-size: 20px;
  padding: 20px 50px 20px 50px;
  border-radius: 8px;
  filter: drop-shadow(0px 8px 10px rgba(0, 0, 0, 0.6));
  margin-bottom: 30px;
  font-weight: 400;
font-size: 20px;
line-height: 22px;
    border: 0;
    margin-top: 90px;
    @include mobile{
      font-size: 17px;
    }
  }
.form{
  text-align: center;
}
