.company{
  background-color: rgb(255, 255, 255);
  padding-top: 80px;
  padding-bottom: 80px;
  @include tablet{
    padding-top: 0px;
  }
}
.company__title{
  color: black;
  margin: 0;
  font-size: 65px;
  @include small-tablet{
    text-align: center;
  }
}
.company__info{
  width: 600px;
  @include tablet{
    width: 400px;
  }
  @include small{
   width: 300px;
  }
}
.company__video{
  width: 600px;
  text-align: right;
  filter: drop-shadow(2px 8px 10px rgba(0, 0, 0, 0.6));
  @include tablet{
    width: 400px;
  }
  @include small{
    width: 300px;
  }
}
.container__company{
  display: flex;
   align-items: center;
   margin-top: 100px;
   justify-content: space-between;
   @include small-tablet{
    flex-direction: column;
  }
}
.company__video__video{
border-radius: 15px;
width: 500px;
@include tablet{
  width: 400px;

}
@include small{
  width: 300px;
}
}

.company__descr{
  font-size: 23px;
  font-family: 'PT';
  @include small-tablet{
    text-align: center;
  }
  @include small{
    font-size: 20px;

  }
}
