.button_item{
  width: 305px;
  height: 180px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  background-color: black;
  border-radius: 10px;
  filter: drop-shadow(2px 8px 10px rgba(0, 0, 0, 0.6));
  cursor: pointer;
  @include tablet{
    &:not(:last-child){
      margin-bottom: 30px;
    }
  }
}
.complects_button{
  display: flex;
  justify-content: space-between;
  margin-bottom: 80px;
  @include tablet{
    flex-direction: column;
    align-items: center;
  }
}
.complects{
  padding-top: 100px;
  padding-bottom: 100px;
}
.complects__title{
  text-align: center;
  font-size: 60px;
  margin-bottom: 80px;
  @include mobile{
    font-size: 35px;
 }
}
.button_title{

  text-align: center;
  color: #94794E;
  margin-top: 25px;
font-size: 25px;

}
.button_descr{
  text-align: center;
  color: white;
font-size: 16px;
line-height: 18px;
  padding: 0 20px 0 20px;
  margin-top: 0;
}
.button_svg{
  transform: rotate(180deg);
  transition: transform 1s;
  margin-bottom: 20px;
}
.complect_info_title{
  text-align: center;
  margin: 0;
  margin-bottom: 50px;
  font-size: 40px;
  line-height: 72px;
  @include mobile{
    line-height: 40px;
 }
}
.complect_info{
  overflow: hidden;
  max-height: 0;
  transition: ease-in max-height 0.9s;
}

.complects_info_active{
  max-height: 600px;
  transition: ease-in max-height 0.9s;
  @include small-tablet{
    max-height: 830px;
   }
   @include mobile{

      max-height: 930px;

   }
}
.complect_info_item{
  font-weight: 400;
font-size: 32px;
line-height: 36px;

}
.complect_info_block img{
  width: 500px;
  height: 250px;
  object-fit: cover;
}
.complect_info_block{
  display: flex;
  justify-content: space-between;
  align-items: center;
  @include small-tablet{
   flex-direction: column;
  }
}
.complect_info_img{
  border-radius: 50px;
}
.complect_info_price{
  margin-top: 60px;
  text-align: center;
  font-size: 35px;
  letter-spacing: 0.1em;
  span{
    letter-spacing: 0.05em;
    color: green;
  }
  @include mobile{
    font-size: 29px;
 }
}
.complect_info_button{
  cursor: pointer;
  background-color: black;
  color: white;
  font-size: 20px;
  padding: 18px 100px 18px 100px;
  border-radius: 8px;
  filter: drop-shadow(0px 8px 10px rgba(0, 0, 0, 0.6));
  margin-bottom: 30px;
  border: none;
  @include mobile{
    padding: 18px 75px 18px 75px;
 }
}
.button_center{
text-align: center;
}

.rotate_button{
  transform: rotate(0deg);
  transition: transform 1s;
}
.transition{
  transition-delay: 0.9s;
}
.complects_list{
text-align: center;
background-color: black;
 max-width: 550px;
 margin: 0 auto 0 auto;
 padding: 20px 28px 20px 28px;
 box-shadow: 0px 10px 8px rgba(0, 0, 0, 0.6);
border-radius: 8px;
cursor: pointer;
margin-bottom: 100px;
@include small{
  padding: 15px 20px 15px 20px;
 }
}
.complects_list-title{
  color:white;
  margin: 0;
  font-weight: 400;
font-size: 20px;
line-height: 22px;

height: 25px;
@include small{
  font-size: 17px;
 }
}

.complects_list-line{
margin: 0;
border: 0;
height: 2px;
  border-radius: 1px;
  background-color: #94794E;
}

.image_list{
  width: 16px;
  height: 16px;
  transition: transform 1s ease;
}
.complects_list-item{
  display: flex;
  justify-content: space-between;
}
.complect_underline{
  height: 4px;
  border-radius: 3px;
  border: 0;
  background-color: black;
  width: 250px;
}
.complects_info-open-1{
  max-height: 128px;
  transition: max-height 0.6s ease;
  overflow: hidden;

  @include small{
    max-height: 152px;
  }
}
.complects_info-open-2{
  max-height: 176px;
  transition: max-height 0.6s ease;
  overflow: hidden;
  @include small-tablet{
    max-height: 224px;
  }
}
.complects_info-open-3{
  max-height: 128px;
  transition: max-height 0.6s ease;
  overflow: hidden;
}
.complects_info-open-4{
  max-height: 84px;
  transition: max-height 0.6s ease;
  overflow: hidden;
  @include small{
    max-height: 125px;
  }
}
.complects_info-close{
  overflow: hidden;
  max-height: 0px;
  transition: max-height 0.6s ease;
}
.complects_info-item{
  color: white;
  font-weight: 400;
font-size: 15px;
line-height: 24px;
text-align: left;
}
.list-item_1{
  padding-top: 10px;
  padding-bottom: 10px;
}
.list-item_2{
  padding-top: 10px;
  padding-bottom: 10px;
}
.list-item_3{
  padding-top: 10px;
  padding-bottom: 10px;
}

.list-item_4{
  padding-top: 10px;
  padding-bottom: 10px;
}
.complects_info-list li::marker{
  color: #94794E;
}
.image_rotate{
  // transition: transform 1s ease;
  transition: all 1s ease;
  fill: red;
  transform: rotate(45deg);
}




