.burger{
  width: 45px;
  height: 45px;
  border: solid 1px rgba(255,255,255, 0.08);
  border-radius: 10px;
  position: relative;
  display: none;
  @include small-tablet{
    display: inline;
  }
}
.burger__line{
  position: absolute;
  background-color: white;
  border-radius: 100px;
  height: 2px;
  width: 26px;
  left: 50%;
  top: 50%;
    transform: translateX(-50%) translateY(-50%);

  &:first-child{
margin-top: -7px;
  }
  &:last-child{
    margin-top: 7px;
    width: 13px;
    transform: translateY(-50%);
  }
}
.burger-close{
  display: none;
  position: absolute;
  width: 45px;
  height: 45px;
  top: 20px;
  border: solid 1px rgba(255,255,255, 0.08);
  border-radius: 10px;
  background-image: url("/img/svg/close.svg");
  background-position: center;
  background-size: 14px 12px;
  background-repeat: no-repeat;
  right: var(--container-offset);
  @include small-tablet{
    display: inline;
  }
}
