.info{
background-color: black;
max-height: 848px;

}
.container__info{
  position: absolute;
  top: 500px;
  left: 50%;
  transform:translate(-50%, -50%);
  width: 1400px;
  margin: 0;
  @include big-desktop{
    width: 1000px;
  }
  @include tablet{
    width: 700px;
  }
  @include small-tablet{
    width: 420px;
  }
  @include small-mobile{
    width: 319px;
  }
}
.bgvideo{

  right: 0;
  top: 0;
  bottom: 0;
  min-width: 100%;
  min-height: 100%;

}


.info__title{
  color: #94794e;
  margin: 0;
  text-align: center;
  font-weight: 400;
  font-size: 130px;

  line-height: 1.1;
@include big-desktop{
  font-size: 80px;
}
@include small-mobile{
  width: 319px;
  margin-bottom: 20px;
}
}

.info__descr{

  color: rgb(148, 121, 78);
font-size: 90px;
  text-align: center;
   font-family: 'PT';
  margin: 0;
  @include big-desktop{
    font-size: 60px;
  }
  @include small-tablet{
    font-size: 40px;
  }

}

