.nav__link{
  color: white;
  @include small-tablet{
    font-size: 35px;
  }
}
.nav__list{
display: flex;
padding: 0;
@include small-tablet{
  @include flex-all-center;
  flex-direction: column;
}

}
.nav__item{
  list-style-type: none;

  &:not(:last-child){
    margin-right: 30px;
    @include small-tablet{
      margin-right: 0;
      margin-bottom: 35px;

    }
  }

}
.nav{
  display: flex;
  justify-content: space-between;
  @include small-tablet{
    position: fixed;
    left: 0;
    top: 0;
    z-index: 500;
    overflow-y: auto;
    width: 100%;
    height: 100%;
    background-color: black;
    @include flex-all-center;
      opacity: 0;
        visibility: hidden;
        transition: opacity 0.4s ease-in-out, visibility 0.4s ease-in-out;


   }

}
.logo{
 object-fit: none;
}
.menu--active{
  opacity: 1;
  visibility: visible;

}
.header-container{
  display: flex;
  justify-content: space-between;
}
