.contacts{
  background-color: black;
  padding-top: 60px;
  padding-bottom: 70px;
}
.contacts_block{
  display: flex;
  justify-content: space-between;
  @include small-tablet{
    flex-direction: column;
  }
}
.contacts_info-title{
  font-weight: 400;
font-size: 32px;
line-height: 41px;
color: #94794E;
margin: 0 0 10px 0;
&:not(:first-child){
  margin-top: 30px;
}
}
.contacts_map{
  height: 430px;
  width: 530px;
  background-color: white;
  border-radius: 12px;
  @include tablet{
    width: 450px;
    height: 350px;
  }
  @include small-tablet{
    margin: 0 auto;
    margin-bottom: 30px;
    width: 400px;
    height: 300px;
  }
  @include mobile{
    width: 300px;
    height: 200px;
  }
}
#map > ymaps {
  border-radius: 12px;
  overflow: hidden;
  }
.contacts_info-descr{
  color: white;
  font-family: 'PT Sans';
font-weight: 400;
font-size: 25px;
line-height: 32px;
}
.contacts_info{
  text-align: center;

}
.contacts_info-svg{
  display: flex;
}
.contacts_svg{
  text-align: center;
  &:not(:last-child){
    margin-right: 40px;
  }
  cursor: pointer;
}
.contacts_info-svg{
  display: flex;
  justify-content: center;
}
