.comments{
  padding-top: 50px;
  background-color: black;
  padding-bottom: 50px;
}
.comments_title{
  color: white;
  text-align: center;
  font-size: 60px;
  margin: 0;
  margin-bottom: 75px;
}
.comment_name{
  font-weight: 400;
font-size: 24px;
line-height: 27px;
color: #94794E;
margin: 0;
}
.comments_block{
  display: flex;
  justify-content: space-between;
  align-items: center;
  @include mobile{
    flex-direction: column;
  }
}
.comment_descr{
  margin-top: 35px;
  font-weight: 400;
font-size: 20px;
line-height: 22px;
color: #FFFFFF;
@include tablet{
  font-size: 17px;
}
}

.comment_line{
  height: 2px;
  background-color: white;
  border-radius: 10px;
}
.comments_block_comment{
  margin-left: 80px;
  @include mobile{
    margin: 0;
    text-align: center;
  }
}

.comment_controls{
  margin-top: 30px;
  display: flex;
  justify-content: center;
}
.controls_next{
  margin-left: 140px;
}
.swiper-button-prev{
  position: relative;
  width: 50px;
  height: 50px;
}
.swiper-button-next{
  position: relative;
  width: 50px;
  height: 50px;
}
.comments_img{
  @include tablet{
    height: 210px;
    width: 210px;
  }
  @include mobile{
    margin-bottom: 30px;
  }
}
